import { createSlice } from '@reduxjs/toolkit';

/**
 * Returns a random integer between min (inclusive) and max (inclusive).
 * The value is no lower than min (or the next integer greater than min
 * if min isn't an integer) and no greater than max (or the next integer
 * lower than max if max isn't an integer).
 * Using Math.round() will give you a non-uniform distribution!
 */
 function getRandomInt(min: number, max: number) : number {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1)) + min;
}

export interface BackgroundState {
  current: number
  count: number
}

const initialState: BackgroundState = {
  current: getRandomInt(1, 10),
  count: 10,
};

function changeBackground(n: number) {
  let elem = document.getElementsByTagName("html")[0];
  elem.className = `bg${n}`;
}

export const backgroundSlice = createSlice({
  name: 'background',
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    cycle: (state) => {
      state.current = state.current === state.count ? 1 : (state.current + 1);
      changeBackground(state.current);
    },
  },
});

export const { cycle } = backgroundSlice.actions;

export default backgroundSlice.reducer;
