export interface QuestionsState {
  currentIndex: number
  current: question,
  total: number,
  score: number,
  completed: boolean,
  started: boolean,
  sortedQuestions: question[],
}

const initialState: QuestionsState = {
  currentIndex: 0,
  current: questions[0],
  total: questions.length,
  score: 0,
  completed: false,
  started: false,
  sortedQuestions: [...questions],
};

export const questionsSlice = createSlice({
  name: 'questions',
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    cycle: (state, action:PayloadAction<{answer: questionAnswer}>) => {
      if (state.completed) {
        return
      }
      state.completed = state.currentIndex+1 === state.total;
      if (action.payload.answer === state.current.answer) {
        state.score += 1;
      }
      state.currentIndex = state.currentIndex === state.total - 1 ? state.currentIndex : (state.currentIndex + 1);
      state.current = state.sortedQuestions[state.currentIndex];
    },
    start: (state) => {
      const sorted = shuffleArray([...questions]);
      state.sortedQuestions = sorted
      state.current = sorted[0];
      state.started = true
    },
  },
});

export const { cycle, start } = questionsSlice.actions;

export const getQuestionsState = (state: RootState) => state.questions;

export default questionsSlice.reducer;
