
import React from 'react';
import { useAppDispatch, useAppSelector } from '../../app/hooks';
import { cycle as cycleBackground } from '../background/backgroundSlice';
import { Intro } from './Intro';
import { Outro } from './Outro';
import { Question } from './Question';
import { cycle, getQuestionsState, start } from './questionSlice';

export interface QuestionsIteratorProps {
}

export function QuestionsIterator(props: QuestionsIteratorProps) {
  const dispatch = useAppDispatch();
  const { currentIndex, current, total, score, completed, started } = useAppSelector(getQuestionsState);

  if (!started) {
    return <Intro 
      onStart={() => dispatch(start())}
    />
  }

  if (started && completed) {
    return <Outro
      score={score}
      total={total}
    />
  }
  
  return (
    <>
      <Question
        q={current}
        index={currentIndex}
        total={total}
        onAnswer={(answer) => {
          dispatch(cycleBackground());
          dispatch(cycle({answer}));
        }}
      />
    </>
  );
}
