import React from 'react';
import style from "./Question.module.scss";

export interface OutroProps {
  score: number,
  total: number,
}

export function Outro(props: OutroProps) {
  const [ contactText, setContactText ] = React.useState("@contattami");
  const [ contactCnt, setContactCnt ] = React.useState(0);
  const numClicks = 50;

  const resolveContactText = () => {
    if (contactCnt < numClicks) {
      setContactCnt(contactCnt + 1);
      setContactText(`Click me ${numClicks-contactCnt} more time${numClicks-contactCnt !== 1 ? 's' : ''}`);
    } else {
      setContactText(atob('bWFya2Rvd24gW0B'+(new Date().getUTCFullYear() > 0 ? 'dIGdteCBbZG9' : '')+'0XSBjb20='));
    }
  }

  return (
    <>
      <div className={style.question}>
        <span className={style.text2}>
            Il tuo punteggio è
            <div className={style.scoreBox}>
              <span className={style.text3}>{props.score}</span>
              <span>&nbsp;&nbsp;su&nbsp;</span>
              <span className={style.text3}>{props.total}</span>
            </div>
            Ti ricordo che questo sito non è di <a href="https://www.instagram.com/teahacic/" target="_blank">@teahacic</a>.<br/>
            Si tratta di un contenuto no-profit, fun-made, senza nessun messaggio<br/>
            politico, sociale, religioso o filosofico nascosto.<br/>
            Per favore non rompete i coglioni.
            
            <div className={style.contactMeBox}>
              <a href="#" onClick={resolveContactText}>{contactText}</a>
            </div>
        </span>
      </div>
    </>
  );
}
