import { Grid } from '@material-ui/core';
import React from 'react';
import style from "./Question.module.scss";
import { question, questionAnswer } from './questionSlice';

export interface QuestionProps {
  q: question,
  index: number,
  total: number,
  onAnswer: (answer: questionAnswer) => void,
}

export function Question(props: QuestionProps) {
  return (
    <>
      <div className={style.question}>

        <span className={style.text}>
          {props.q.text}
        </span> 

        <span className={style.progress}>
          {
            props.total === props.index + 1
            ?
            <span>Siamo alla fine</span>
            :
            <span>Ancora {props.total-props.index} cose</span>
          }
          
        </span>
      </div>
      <Grid container xs={12}>
        <Grid item xs={6}>
          <div className={style.button} role="gay" onClick={() => props.onAnswer(questionAnswer.gay)}>Gay</div>
        </Grid>

        <Grid item xs={6}>
          <div className={style.button} role="not-gay" onClick={() => props.onAnswer(questionAnswer.notGay)}>Not Gay</div>
        </Grid>
      </Grid>
    </>
  );
}
